/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import WidgetCard from 'components/Cards/WidgetCard';
import { useStaticQuery, graphql, Link } from 'gatsby';
import { Group } from 'models/post';
import { Chip, Avatar, makeStyles } from '@material-ui/core';
import { linkEvent } from 'utils/loggers';
import { useCallback } from 'react';
import { toKebabCase } from 'utils/string';

const TagsCard = () => {
  const chipStyle = makeStyles({
    root: {
      fontSize: '0.8rem',
      marginRight: '0.5rem',
      marginBottom: '0.5rem',
    },
  })();
  const {
    allMarkdownRemark: { group: tags },
  }: Group = useStaticQuery(graphql`
    query TagsQuery {
      allMarkdownRemark {
        group(field: frontmatter___tags) {
          fieldValue
          totalCount
        }
      }
    }
  `);

  const handleTagClick = useCallback((url: string) => {
    linkEvent(url, '태그 링크 클릭');
  }, []);

  return (
    <WidgetCard title="태그">
      {tags.map(tag => (
        <Link
          key={tag.fieldValue}
          to={`/tags/${toKebabCase(tag.fieldValue)}`}
          css={css`
            text-decoration: none;
          `}
        >
          <Chip
            className={chipStyle.root}
            size="small"
            label={tag.fieldValue}
            avatar={<Avatar>{tag.totalCount}</Avatar>}
            onClick={() => handleTagClick(`/tags/${toKebabCase(tag.fieldValue)}`)}
          />
        </Link>
      ))}
    </WidgetCard>
  );
};

export default TagsCard;
