/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { Card, CardContent, Box, Typography, Grid, Button, withStyles } from '@material-ui/core';
import PostCount from 'components/Utils/PostCount';
import CategoryCount from 'components/Utils/CategoryCount';
import TagCount from 'components/Utils/TagCount';
import { Colors } from 'utils/colors';
import * as Icon from 'react-feather';

const Bio = () => {
  const data = useStaticQuery(graphql`
    query {
      avatar: file(absolutePath: { regex: "/me.jpg/" }) {
        childImageSharp {
          fixed(width: 128, height: 128, cropFocus: CENTER) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
            email
          }
          social {
            github
            linkedin
          }
        }
      }
    }
  `);

  const {
    avatar: {
      childImageSharp: { fixed: avatar },
    },
    site: {
      siteMetadata: { author, social, email },
    },
  } = data;

  return (
    <Card>
      <CardContent
        css={css`
          text-align: center;
        `}
      >
        <Image
          fixed={avatar}
          alt={author.name}
          css={css`
            min-width: 50px;
            border-radius: 100%;
            margin-bottom: 1rem;
          `}
          imgStyle={{
            borderRadius: `50%`,
          }}
        />
        <Box
          css={css`
            margin-bottom: 2rem;
          `}
        >
          <Typography variant="h6" component="h6">
            {author.name}
          </Typography>
          <Typography color="textSecondary" variant="subtitle2">
            {author.summary}
          </Typography>
        </Box>
        <Grid container>
          <Count item xs={4}>
            <dt>포스트</dt>
            <dd>
              <PostCount />
            </dd>
          </Count>
          <Count item xs={4}>
            <dt>카테고리</dt>
            <dd>
              <CategoryCount />
            </dd>
          </Count>
          <Count item xs={4}>
            <dt>태그</dt>
            <dd>
              <TagCount />
            </dd>
          </Count>
        </Grid>
        <Box
          css={css`
            margin-bottom: 1rem;
          `}
        >
          <EmailButton
            variant="contained"
            css={css`
              width: 100%;
            `}
            href={`mailto:${email}`}
          >
            <Icon.Mail
              css={css`
                width: 1rem;
                margin-right: 0.5rem;
              `}
            />
            이메일
          </EmailButton>
        </Box>
        <Grid container>
          <Grid item xs={4}>
            <ExternalLinkButton href={social.github} target="_blank">
              <Icon.GitHub />
            </ExternalLinkButton>
          </Grid>
          <Grid item xs={4}>
            <ExternalLinkButton href={social.linkedin} target="_blank">
              <Icon.Linkedin />
            </ExternalLinkButton>
          </Grid>
          <Grid item xs={4}>
            <ExternalLinkButton href={social.rss} target="_blank">
              <Icon.Rss />
            </ExternalLinkButton>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

const Count = styled(Grid)`
  dt {
    color: ${Colors.Grey['600']};
    font-size: 0.7rem;
    margin-bottom: 0.5rem;
  }
  dd {
    font-size: 2rem;
    font-weight: 300;
  }
`;

const EmailButton = withStyles(theme => ({
  root: {
    color: theme.palette.getContrastText(Colors.Grey['800']),
    backgroundColor: Colors.Grey['800'],
    '&:hover': {
      backgroundColor: Colors.Grey['900'],
    },
  },
}))(Button);

const ExternalLinkButton = styled(Button)`
  width: 100%;
`;

export default Bio;
